@import '../../../../variables';

.info-bar {
  z-index: 1;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: lightblue;

  &.medium-and-larger {
    display: none;
  }

  @media screen and (min-width: $medium-breakpoint) {
    display: none;

    &.medium-and-larger {
      width: $info-bar-width-lg-screen;
      display: block;
    }
  }
}